header {
  margin-top: 6rem;
  height: calc(100vh - 5rem);
  display: grid;
  place-items: center;
  background: var(--color-white);
  transition: var(--transition);
}

.header-back {
  position: absolute;
  background-color: var(--color-light);
  width: 100%;
  height: 55%;
  clip-path: polygon(0 0, 100% 0, 100% 65%, 0 100%);
  top: 0;
}

.header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.header__profile {
  width: 20rem;
  height: 25rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
  border: 1rem solid var(--color-light);
}

.header__profile img {
  filter: grayscale(0.65);
}

.header__container p {
  width: 35rem;
  text-align: center;
  margin: 0.6rem 0 2rem;
}

.header__cta {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
  header {
    margin-top: 0;
    height: 100vh;
  }
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 600px) {
  .header__profile {
    width: 14rem;
    height: 17rem;
    max-width: 60%;
    max-height: 35vh;
  }

  .header__container p {
    width: 100%;
  }

  .header__cta {
    gap: 1rem;
  }
}
